import React, { Component } from 'react';
import './App.css';
import Main from "./components/Main";
import {Layout,Header, Navigation, Content, Drawer} from "react-mdl";

class App extends Component {
  render() {
    return (
            <div className="demo-big-content">
              <Layout>
                <Header className="header-color" title={<a to="/" style={{textDecoration:"none", color:"#fff"}}>My Portfolio</a>} scroll>
                  <Navigation>
                    <a href="/taylor-yao/">Home</a>
                    <a href="/aboutme">About Me</a>
                    <a href="/resume">Resume</a>
                    <a href="/projects">Projects</a>
                    <a href="/contact">Contact</a>                                
                  </Navigation>
                </Header>
                <Drawer title={<a href="/" style={{textDecoration:"none", color:"#000"}}>My Portfolio</a>}>
                  <Navigation>
                    <a href="/taylor-yao/">Home</a>
                    <a href="/aboutme">About Me</a>
                    <a href="/resume">Resume</a>
                    <a href="/projects">Projects</a>
                    <a href="/contact">Contact</a>                                                               
                  </Navigation>
                </Drawer>
                <Content >
                <Main/>
                    
                </Content>
              </Layout>
        </div>
    );
  }
}

export default App;



