import React, {Component} from "react";
import {Grid, Cell, List, ListItemContent, ListItem} from "react-mdl";

class Contact extends Component {
    render(){
        return(
            <div className="contact-body">
                <Grid className="contact-grid">
                    <Cell col={6}>
                    <h2>Taylor .A. Yao</h2>
                    <img src={require("../Images/taylor-yao.JPG")} alt="Avatar" style={{height:"150px", borderRadius:"50%"}}/>
                    <p style={{width:"75%", margin:"auto", paddingTop:"1em"}}>
                        We were Christian family of 15 people in the village of Pagala-gare in the West African country of Togo. 
                        My father was a teacher and my mother runs a little restaurant. 
                        Pagala-gare was made up with several tribes so my education includes 5 native languages (Akposso, Ewe, Mina, Kotokoli, Kabye) 
                        beside our official language which is French. I spend a lot of time translating and solving problems in between those fellow 
                        villagers who found it difficult to communicate with each other. I also worked for an NGO (ONG Future Foundation Togo) putting 
                        on social and educational programs for children that did not have the advantage of a stable family grouping.
                        <br/>
                            In 2013 I was stile I moved to Guinea to become a semi-professional soccer player, unfortunately I was injured in a game which cut my dream of soccer stardom,
                        so I decided to enroll in the University to study sociology in 2014. After a year my name was selected by the US Diversity Visa program to move to the US and 
                        I currently enroll in Coding Boot-Camp at Georgia Institute of Technology. What a journey, and it just started!
                       </p>
                    </Cell>                
                    <Cell col={6}>
                        <h2>Contact Me</h2>
                        <hr/>
                        <div className="contact-list">
                            <List>
                                <ListItem>
                                    <ListItemContent style={{fontFamily:"Anton", fontSize:"25px;"}}>
                                    <i className="fa fa-phone-square" aria-hidden="true"/>
                                    +1 404 862 9078
                                    </ListItemContent>
                                </ListItem>

                                <ListItem>
                                    <ListItemContent style={{fontFamily:"Anton", fontSize:"25px;"}}>
                                    <i className="fa fa-envelope" aria-hidden="true"/>
                                    yaotaylor@gmail.com
                                    </ListItemContent>
                                </ListItem>

                                <ListItem>
                                    <ListItemContent style={{fontFamily:"Anton", fontSize:"25px;"}}>
                                    <i className="fab fa-skype" aria-hidden="true"/>
                                    tayloryao25
                                    </ListItemContent>
                                </ListItem>
                            </List>

                        </div>
                        
                    </Cell>                
                </Grid>
            </div>
        )
    }
}

export default Contact;


